<template>
  <div class="userDate">
    <el-button @click="back">返回</el-button>
    <h3>本周排班人员情况一览表</h3>
    <div class="address">
      <div class="left">地区：</div>
      <user-address
        :defaultValue="defaultAddress"
        ref="AddAddress"
        @countryChange="getPerson"
        seletStyle="width:220px"
      />
    </div>
    <div class="list" v-for="(patient, index) in patientList" :key="patient">
      <div class="name">{{patient.userName}}</div>
      <div class="list-date">
        <el-checkbox-group v-model="patient.workTimes">
          <el-checkbox v-for="date in dateMorList" :label="date.id" :key="date.id">{{date.name}}</el-checkbox>
        </el-checkbox-group>
      </div>
      <div class="list-date">
        <el-checkbox-group v-model="patient.workTimes">
          <el-checkbox v-for="date in dateAfterList" :label="date.id" :key="date.id">{{date.name}}</el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script>
const queryString = require("querystring");
import UserAddress from "../../../../components/address";
export default {
  name: "userDate",
  data() {
    return {
      defaultAddress: null,
      patientList: [],
      dateMorList: [
        { id: "11", name: "星期一上午" },
        { id: "21", name: "星期二上午" },
        { id: "31", name: "星期三上午" },
        { id: "41", name: "星期四上午" },
        { id: "51", name: "星期五上午" }
      ],
      dateAfterList: [
        { id: "12", name: "星期一下午" },
        { id: "22", name: "星期二下午" },
        { id: "32", name: "星期三下午" },
        { id: "42", name: "星期四下午" },
        { id: "52", name: "星期五下午" }
      ],
      searchForm: {
        province: "",
        city: "",
        country: ""
      }
    };
  },
  mounted() {
    this.getPerson();
  },
  components: {
    UserAddress
  },
  methods: {
    getPerson() {
      let addressParams = this.$refs.AddAddress.getData();
      this.searchForm.province = addressParams.province;
      this.searchForm.city = addressParams.city;
      this.searchForm.country = addressParams.country;
      this.api
        .get("/groundPerson?" + queryString.stringify(this.searchForm))
        .then(res => {
          if (res.code == 200) {
            this.patientList = res.data.list;
            for (var i = 0; i < this.patientList.length; i++) {
              if (this.patientList[i].workTimes) {
                this.patientList[i].workTimes = this.patientList[
                  i
                ].workTimes.split(",");
              }
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    back() {
      this.$router.back(-1);
    }
  }
};
</script>

<style lang="less" scoped>
.userDate {
  padding-left: 20px;
  h3 {
    margin: 20px 0;
  }
  .address {
    .left {
      margin-bottom: 10px;
    }
  }
  .list {
    margin-top: 20px;
    .list-date {
      margin-top: 10px;
    }
  }
}
</style>
